.wrapper {
  /* margin: 50px 20px; */
  margin: 50px auto;
  width: 320px;
}
.header {
  font-family: 'Caviar Dreams';
  font-size: 22px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 300;
  margin: 0 0 15px 0;
}
.info {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
}

.img {
  display: block;
  width: 320px;
  height: auto;
}

.text {
  order: -1;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  margin: 0 0 30px 0;
}
@media(min-width: 1200px) {
  .wrapper {
    /* margin: 80px 70px 210px 70px; */
    margin: 80px auto 210px auto;
    width: 1170px;
    padding: 0 50px;
  }

  .header {
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 50px;
  }
  .info {
    flex-direction: row;
    width: 100%;
    order: unset;
  }
  .img {
    order: -1;
    margin-right: 100px;
    width: calc(100% - 100px);
    height: auto;
  }
  .text {
    order: unset;
    margin: 0;
    width: 36%;
    font-size: 18px;
    line-height: 30px;
  }
}
